
.contactContainer{
  width: 100%;
  text-align: left;
  margin-left: 220px;
}

header {
  height: 100px;
  background-color: #28c9b9;
  opacity: 0.9;
}

.contactItem {
  margin: 15px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
}

address img {
  height: 30px;
  width: 30px;
  display: block;
}

address div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 60px;
  width: 60px;
  border-radius: 50%;
  background-color: #28c9b9;
}

address p, address a{
  color: #3484ed;
  font-size: 1.5em;
  margin-left: 20px;
  display: block;
}



