
.menuImage {
  height: 150px;
  width: 150px;
  border-radius: 50%;
}

.menuContainer {
  position: fixed;
  height: 100%;
  width: 220px;
  top: 0px;
  left: 0px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  background-image: url('../images/city_background.jpg');
  background-size: 300% 100%;
  background-color: #6389A3;
  background-blend-mode: multiply;
  color: white;
}

.menuContainer p{
  margin: 5px 0;
}

nav a{
  text-decoration: none;
}

.menuItem {
  font-size: 1.2em;
  margin-top: 20px;
  color: white;
  text-decoration: none;
  list-style: none;
  margin-left: 0;
  display: block;
}

.menuItem:hover {
  color: lightBlue;
}

.selectedItem {
  font-size: 1.2em;
  margin-top: 20px;
  display: inline;
  width: 100%;
  color: #28c9b9;
  display: block;
}

.selectedItem::after {
  color: #28c9b9;
  content: '>';
  margin-left: 7px;
}

.selectedItem::before {
  position: absolute;
	border-top: 9px solid transparent;
	border-right: 25px solid white;
	border-bottom: 9px solid transparent;
  right: 0px;
  content: "";
  margin-top: 0px;
}



