
@import url("https://fonts.googleapis.com/css?family=Rubik|Work+Sans");


.aboutContainer{
  text-align: left;
  display: -ms-flexbox;
  display: flex;
  margin-left: 220px;
  -ms-flex-direction: column;
      flex-direction: column;
}

.aboutContainer div{
  max-width: 900px;
  height: 45px;
  margin: 15px;
  margin-left: 40px;
  border: 0px solid white;
  border-left-width: 5px;
}

.aboutContainer h2{
  margin: 5px;
  margin-left: 20px;
  color: white;
  text-align: left;
  font-size: 22px;
}

.aboutHeader {
  height: 200px;
  width: 100%;
  background-color: #28c9b9;
  opacity: 0.9;
}

.introductionTextBody {
  margin: 30px;
  color: black;
  font-size: 1.2em;
  color: darkgrey;
  line-height: 110%;
}

.introductionTextBody p {
  margin-top: 10px;
  text-indent: 30px;
}

.introductionTextBody li{
  margin-left: 70px;
}



